                contentothers:`-Bases de datos:\rPoseo experiencia en el uso de bases de datos SQL y MongoDB. Durante mi carrera universitaria, adquirí un sólido conocimiento en SQL a través de diversos cursos y proyectos académicos. Esta formación me permitió comprender los fundamentos de las bases de datos relacionales, incluyendo la creación de estructuras de datos, la gestión de información y la realización de consultas complejas.\rEn el ámbito profesional, he trabajado con MongoDB durante un par de años. Esta experiencia me ha permitido familiarizarme con las características específicas de este tipo de bases de datos NoSQL, como la flexibilidad en el almacenamiento de datos y la escalabilidad horizontal. He utilizado MongoDB para el desarrollo de aplicaciones web, la gestión de documentos y la analítica de datos.\rA continuación, se detallan algunos ejemplos específicos de mi experiencia:\r
SQL:\r*Diseño e implementación de bases de datos relacionales para proyectos académicos.\r*Realización de consultas complejas para extraer información relevante de las bases de datos.\r*Optimización del rendimiento de las consultas SQL.\rMongoDB:\r*Desarrollo de aplicaciones web que utilizan MongoDB como base de datos.\r*Implementación de esquemas de datos NoSQL para optimizar la flexibilidad y escalabilidad.\r*Realización de consultas y análisis de datos en MongoDB.-Docker:\rDurante mi formación académica, tuve la oportunidad de adquirir experiencia con Docker. Esta experiencia se desarrolló en el contexto de diferentes cursos y proyectos, donde se utilizó Docker para:\rContenerizar aplicaciones: Implementé aplicaciones web y de escritorio en contenedores Docker, lo que me permitió aislarlas del entorno y facilitar su portabilidad y escalabilidad.\rCrear entornos de desarrollo replicables: Utilicé Docker para crear entornos de desarrollo idénticos para cada miembro de un equipo, lo que facilitó la colaboración y la reproducibilidad de los resultados.\rAutomatizar tareas de DevOps: Implementé flujos de trabajo de DevOps utilizando Docker para automatizar tareas como la construcción, la integración y la implementación de aplicaciones.\r-GitHub: 4 años de carrera y 2 años profesionales\rPoseo una amplia experiencia en el uso de GitHub, que abarca 4 años de mi carrera universitaria y 2 años de trabajo profesional. Durante este tiempo, he utilizado GitHub para una variedad de propósitos tanto personales como profesionales.\r-Otros:\rDurante mi experiencia además de los ya mencionados he trabajado en otros proyectos con diferentes lenguajes como C, C# o C++, sin embargo, no he trabajado directamente de manera profesional en estos campos, no lo descartaría en un futuro\r`,
